.about-us-vision-wrapper {
  width: 100%;
  background-color: #fff;
  padding: 50px 20px;
  box-sizing: border-box;
}

.about-us-vision {
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
}

.about-us-vision h2 {
  text-align: left;
  font-size: 36px;
  color: #020101;
  margin-bottom: 40px;
  font-family: 'Roboto', sans-serif;
}

.about-us-vision-description {
  font-size: 18px;
  color: #000;
  margin-bottom: 40px;
  line-height: 1.8;
  text-align: left;
  font-family: 'Roboto', sans-serif;
}

.about-us-vision-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 20px;
}

.about-us-vision-card {
  background-color: #fff;
  border-radius: 16px;
  box-shadow: 4px 4px 12px 0px rgba(0, 0, 0, 0.08);
  padding: 36px 20px;
  width: calc(33.333% - 20px);
  text-align: center;
  box-sizing: border-box;
}

.about-us-vision-card-number {
  font-family: 'Roboto', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 2px solid #FF0085;
  color: #FF0085;
  font-size: 40px;
  font-weight: 400;
  margin: 0 auto 20px;
  animation: ringAnimation 2s infinite;
}

@keyframes ringAnimation {
  0% {
    transform: scale(.95);
    box-shadow: 0 0 0 0 #FF008521;
  }
  70% {
    transform: scale(1);
    box-shadow: 0 0 0 20px #FF008521;
  }
  100% {
    transform: scale(.95);
    box-shadow: 0 0 0 0 #FF008521;
  }
}

.about-us-vision-card-number span {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.about-us-vision-card-text {
  font-size: 18px;
  color: #8B8D8F;
  line-height: 1.8;
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

/* Mobile view */
@media (max-width: 767px) {
  .about-us-vision-cards {
    flex-direction: column;
    align-items: center;
  }

  .about-us-vision-card {
    width: 100%;
    margin-bottom: 20px;
  }
}
