@import url('../../fonts/fonts.css');

.section-title {
  text-align: left;
  position: relative;
  display: inline-block;
}

.section-title h2 {
  color: var(--e-global-color-64a0afa);
  font-family: 'Roboto', sans-serif;
  font-size: 36px;
  font-weight: bold;
  padding: 0;
  margin: 0;
  line-height: 1;
  display: inline-block;
}

.divider {
  width: 70%;
  border-top: 2px solid #000; /* Default color */
  margin: 10px 0;
  position: relative;
  left: -100%; /* Initial state for animation */
  opacity: 0; /* Initial state for animation */
}

.divider.in-view {
  left: 0;
  opacity: 1;
  transition: left 2s ease, opacity 2s ease;
}

@media (max-width: 767px) {
  .section-title h2 {
    font-size: 28px !important;
  }
}
