.ds-models-collaboration-section {
  text-align: center;
  padding: 50px 20px;
  background-color: #fff;
  max-width: 1200px;
  margin: 0 auto;
  font-family: 'Roboto', sans-serif;
}

.ds-models-cards-container {
  display: grid;
  gap: 20px;
  grid-template-columns: 1fr; /* Default to single column */
}

.ds-models-card-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.ds-models-card {
  background: #fff;
  border-top: 6px solid #3a8bc8;
  border-radius: 10px;
  box-shadow: 0 0px 10px 0px rgba(0, 0, 0, 0.2);
  padding: 15px;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  box-sizing: border-box;
  transition: background-color 0.3s ease;
  margin: 0px;
  text-align: left;
}

.ds-models-card:hover {
  background-color: #f7f7f7;
}

.ds-models-card h3 {
  text-align: center;
  font-size: 20px;
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
  color: #333;
  margin-bottom: 10px;
}

.ds-models-card p {
  font-size: 16px;
  font-family: 'Roboto', sans-serif;
  color: #6F6F6F;
  line-height: 1.8;
}

.ds-models-card ol {
  padding-left: 28px;
}

.ds-models-card ol li {
  margin-bottom: 0px;
  font-size: 16px;
  font-family: 'Roboto', sans-serif;
  color: #6F6F6F;
  line-height: 1.8;
}

.ds-models-card img {
  width: 50px;
  height: 50px;
  margin-bottom: 10px;
}

@media (min-width: 768px) {
  .ds-models-cards-container {
    grid-template-columns: repeat(2, 1fr); /* Two columns for larger screens */
  }
}
